import styled from 'styled-components';

const OptionContainer = styled.div`
    display: flex;
    align-items: center;
    margin-top: 15px;
    color: grey;
    cursor: pointer;
    padding: 10px;
    &:hover {
         background-color: whitesmoke;
         border-radius: 10px;
    }
`
const H4 = styled.h4`
    margin-left: 5px;
`


export default function InputOption({ title, Icon, color }) {
    return (
        <OptionContainer>
            <Icon style={{ color: color }} />
            <H4>{title}</H4>
        </OptionContainer>
    )
}
