import { forwardRef } from 'react';
import { Avatar } from '@material-ui/core';
import styled from 'styled-components';
import ThumbUpOutlinedIcon from '@material-ui/icons/ThumbUpOutlined';
import ChatOutlinedIcon from '@material-ui/icons/ChatOutlined';
import ShareOutlinedIcon from '@material-ui/icons/ShareOutlined';
import SendOutlinedIcon from '@material-ui/icons/SendOutlined';
import InputOption from './InputOption';


const PostContainer = styled.main`
    background-color: white;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 10px;
`
const PostHeader = styled.div`
    display: flex;
    margin-bottom: 10px;
`
const PostInfo = styled.div`
    margin-left: 10px;
`
const H2 = styled.h2`
    font-size: 1rem;
`
const P = styled.p`
    font-size: 0.8rem;
    color: grey;
`
const PostBody = styled.div`
    overflow-wrap: anywhere;
`
const Span = styled.span`

`
const PostButtons = styled.div`
    display: flex;
`


const Post = forwardRef(({ name, description, message, photoUrl }, ref) => {
    return (
        <PostContainer ref={ref}>
            <PostHeader>
                <Avatar src={photoUrl}>{name[0]}</Avatar>
                <PostInfo>
                    <H2>{name}</H2>
                    <P>{description}</P>
                </PostInfo>
            </PostHeader>

            <PostBody>
                <Span>{message}</Span>
            </PostBody>

            <PostButtons>
                <InputOption Icon={ThumbUpOutlinedIcon} title="Like" color="grey" />
                <InputOption Icon={ChatOutlinedIcon} title="Comment" color="grey" />
                <InputOption Icon={ShareOutlinedIcon} title="Share" color="grey" />
                <InputOption Icon={SendOutlinedIcon} title="Send" color="grey" />
            </PostButtons>
        </PostContainer>
    )
})

export default Post;
