import { useEffect } from 'react';
import { useState } from 'react';
import { useDispatch } from 'react-redux';
import styled from 'styled-components';
import { login } from '../features/userSlice';
import linkedinImg from '../images/linkedin.png';
import { auth } from './firebase';
import useStorage from './useStorage';

const LoginContainer = styled.article`
    display: grid;
    place-items: center;
    margin: 0 auto;
    padding: 100px 0;
`
const Img = styled.img`
    object-fit: contain;
    height: 70px;
    margin-top: 20px;
    margin-bottom: 20px;
`
const Form = styled.form`
    display: flex;
    flex-direction: column;
`
const Input = styled.input`
    width: 350px;
    height: 50px;
    font-size: 1.2rem;
    padding-left: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
`
const Label = styled.label`
    font-size: 1.2rem;
    margin-bottom: 10px;
`
const Button = styled.button`
    width: 365px;
    height: 50px;
    font-size: large;
    color: white;
    background-color: #0074b1;
    border-radius: 5px;
    cursor: pointer;
`
const P = styled.p`
    margin-top: 20px;
`
const Span = styled.span`
    color: #0177b7;
    cursor: pointer;
`
const InputFile = styled.input`
    margin-bottom: 10px;
`
const Error = styled.span`
    color: red;
    font-weight: bold;
`
const Valid = styled.span`
    color: green;
    font-weight: bold;
`

export default function Login() {
    const [name, setName] = useState('');
    const [password, setPassword] = useState('');
    const [email, setEmail] = useState('');
    const [picture, setPicture] = useState(null);
    const [error, setError] = useState(null);
    const dispatch = useDispatch();

    const { picUrl } = useStorage(picture);

    useEffect(() => {
        if (picUrl) {
            console.log(picUrl)
            setPicture(null);
        }
        return;
    }, [picUrl, setPicture])

    const types = [
        "image/apng",
        "image/bmp",
        "image/gif",
        "image/jpeg",
        "image/jpg",
        "image/pjpeg",
        "image/png",
        "image/tiff",
        "image/webp"
    ];

    const handlePicture = e => {
        let img = e.target.files[0];

        if (img && types.includes(img.type)) {
            setPicture(img);
            setError(null);
        } else {
            setPicture(null);
            setError('Please select a valid image format');
        }
    }

    const register = () => {
        if (!name) {
            return alert('Please enter a full name');
        }
        auth.createUserWithEmailAndPassword(email, password)
            .then((userAuth) => {
                userAuth.user.updateProfile({
                    displayName: name,
                    photoURL: picUrl
                })
            }).then((userAuth) => {
                dispatch(login({
                    email: userAuth.user.email,
                    uid: userAuth.user.uid,
                    displayName: name,
                    photoUrl: picUrl
                }))
            }).catch(error => alert(error));
    }

    const loginToApp = (e) => {
        e.preventDefault();

        auth.signInWithEmailAndPassword(email, password)
            .then(userAuth => {
                dispatch(login({
                    email: userAuth.user.email,
                    uid: userAuth.user.uid,
                    displayName: userAuth.user.displayName,
                    profileUrl: userAuth.user.photoURL
                }))
            }).catch(error => alert(error));
    }


    return (
        <LoginContainer>
            <Img src={linkedinImg} />

            <Form>
                <Input type="text" value={name} onChange={e => setName(e.target.value)} placeholder="Full name (required if registering)" />
                <Label htmlFor="profile-picture">Choose a profile picture.</Label>
                <InputFile type="file" id="profile-picture" onChange={handlePicture} />
                {picture && <Valid>{picture.name}</Valid>}
                {error && <Error>{error}</Error>}
                <Input type="email" value={email} onChange={e => setEmail(e.target.value)} placeholder="Email" />
                <Input type="password" value={password} onChange={e => setPassword(e.target.value)} placeholder="Password" />
                <Button type="submit" onClick={loginToApp}>Sign In</Button>
            </Form>

            <P>Not a member?
                <Span onClick={register}> Register Now</Span>
            </P>
        </LoginContainer>
    )
}
