import styled from 'styled-components';
// import Avatar from '@material-ui/core/Avatar';


const HeaderOpContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
    cursor: pointer;
    color: grey;
    &:hover {
        color: black;
    }
`
const H3 = styled.h3`
    font-size: 12px;
    font-weight: 400;
`
const Avatar = styled.img`
    height: 40px;
    width: 40px;
    border-radius: 50%;
`

export default function HeaderOption({ avatar, Icon, title, handleLogout }) {
    return (
        <HeaderOpContainer onClick={handleLogout}>
            {Icon && <Icon />}
            {avatar && <Avatar src={avatar} alt="avatar" />}
            <H3>{title}</H3>
        </HeaderOpContainer>
    )
}
