import styled from 'styled-components';
import CreateIcon from '@material-ui/icons/Create';
import InputOption from './InputOption';
import ImageIcon from '@material-ui/icons/Image';
import EventIcon from '@material-ui/icons/Event';
import CalendarViewDayIcon from '@material-ui/icons/CalendarViewDay';
import VideoLibraryIcon from '@material-ui/icons/VideoLibrary';
import Post from './Post';
import { useEffect, useState } from 'react';
import firebase from 'firebase';
import { db } from './firebase';
import { useSelector } from 'react-redux';
import { selectUser } from '../features/userSlice';
import FlipMove from 'react-flip-move';

const FeedContainer = styled.aside`
    flex: 0.6;
    margin: 0 20px;
`
const InputWrapper = styled.div`
    background-color: white;
    padding: 10px;
    padding-bottom: 20px;
    border-radius: 10px;
    margin-bottom: 20px;
`
const InputFeed = styled.div`
    border: 1px solid lightgray;
    border-radius: 30px;
    display: flex;
    padding: 10px;
    color: gray;
    padding-left: 15px;
`
const Form = styled.form`
    display: flex;
    width: 100%;
`
const Input = styled.input`
    border: none;
    flex: 1;
    margin-left: 10px;
    outline: none;
    font-weight: 600;
`
const InputOptions = styled.div`
    display: flex;
    justify-content: space-evenly;
`

export default function Feed() {
    const user = useSelector(selectUser);
    const [posts, setPosts] = useState([]);
    const [input, setInput] = useState('');

    const sendPost = e => {
        e.preventDefault();

        db.collection('posts').add({
            name: user.displayName,
            description: user.email,
            message: input,
            photoUrl: user.photoUrl || "",
            timestamp: firebase.firestore.FieldValue.serverTimestamp()
        })
        setInput('');
    }

    const handleChange = e => {
        setInput(e.target.value);
    }

    useEffect(() => {
        db.collection('posts').orderBy('timestamp', 'desc').onSnapshot(snapshot => (
            setPosts(snapshot.docs.map(doc => (
                {
                    id: doc.id,
                    data: doc.data(),
                }
            )))
        ))
    }, [])

    return (
        <FeedContainer>
            <InputWrapper>
                <InputFeed>
                    <CreateIcon />
                    <Form onSubmit={sendPost}>
                        <Input type="text" value={input} onChange={handleChange} />
                        {/* <Button type="submit">Send</Button> */}
                    </Form>
                </InputFeed>
                <InputOptions>
                    <InputOption Icon={ImageIcon} title="Photo" color="#70B5F9" />
                    <InputOption Icon={VideoLibraryIcon} title="Video" color="#E7A33E" />
                    <InputOption Icon={EventIcon} title="Event" color="#C0CBCD" />
                    <InputOption Icon={CalendarViewDayIcon} title="Write an article" color="#7FC15E" />
                </InputOptions>
            </InputWrapper>
            <FlipMove>
                {posts && posts.map(({ id, data: { name, description, message, photoUrl } }) => (
                    <Post key={id} name={name} description={description} message={message} photoUrl={photoUrl} />
                ))}
            </FlipMove>
        </FeedContainer>
    )
}
