import styled from 'styled-components';
import InfoIcon from '@material-ui/icons/Info';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';

const WidgetsContainer = styled.aside`
    position: sticky;
    top: 80px;
    flex: 0.2;
    background-color: white;
    border-radius: 10px;
    border: 1px solid lightgray;
    height: fit-content;
    padding-bottom: 10px;
`
const WidgetsHeader = styled.header`
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
`
const H2 = styled.h2`
    font-size: 1rem;
    font-weight: 400;
`
const WidgetsArticle = styled.div`
    display: flex;
    padding: 10px;
    cursor: pointer;
    &:hover {
        background-color: whitesmoke;
    }
`
const LeftWidgets = styled.div`
    color: #0177b7;
    margin-right: 5px;
    >svg {
        font-size: 0.9rem;
    }
`
const RightWidgets = styled.div`
    flex: 1;
`
const H4 = styled.h4`
    font-size: 0.9rem;
`
const P = styled.p`
    font-size: 0.8rem;
    color: grey;
`



export default function Widgets() {
    const newsArticle = (heading, subtitle) => (
        <WidgetsArticle>
            <LeftWidgets>
                <FiberManualRecordIcon />
            </LeftWidgets>

            <RightWidgets>
                <H4>{heading}</H4>
                <P>{subtitle}</P>
            </RightWidgets>
        </WidgetsArticle>
    )


    return (
        <WidgetsContainer>
            <WidgetsHeader>
                <H2>LinkedIn News</H2>
                <InfoIcon />
            </WidgetsHeader>
            {newsArticle("Coronavirus : UK updates", "Top news - 886")}
            {newsArticle("Tesla hits new highs", "Cars & auto - 300")}
            {newsArticle("This was made from a tutorial", "by Sonny Sangha")}
            {newsArticle("Is Redux too good?", "Code - 123 readers")}
            {newsArticle("200 new cases of the covid delta branch", "Health - 233 readers")}
        </WidgetsContainer>
    )
}
