import { useEffect, useState } from 'react';
import { st } from './firebase';

const useStorage = (picture) => {
    const [picUrl, setPicUrl] = useState(null);

    useEffect(() => {
        if (!picture) {
            return;
        }
        const stRef = st.ref(picture.name);
        stRef.put(picture).on('state_changed', (snap) => {
            //progress bar here 
            //let percentage = (snap.bytesTransferred / snap.totalBytes) * 100
            //setProgress(percentage)
            console.log(snap)
        }, (err) => {
            //setError(err)
            console.log(err)
        },
            async () => {
                const url = await stRef.getDownloadURL();
                setPicUrl(url);
            })
    }, [picture]);


    return { picUrl };
}
export default useStorage;
