import styled from 'styled-components';
import Avatar from '@material-ui/core/Avatar';
// import avatar from '../images/avatar.jpg';
import img2 from '../images/img2.jpg';
import { useSelector } from 'react-redux';
import { selectUser } from '../features/userSlice';

const SidebarContainer = styled.aside`
    top: 80px;
    position: sticky;
    flex: 0.2;
    border-radius: 10px;
    text-align: center;
    height: fit-content;
`
const SidebarTop = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid lightgray;
    border-bottom: 0;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background-color: white;
    padding-bottom: 10px;
    overflow: hidden;
    >svg {
        margin-bottom: 10px;
    }
`
const Img = styled.img`
    margin-bottom: -20px;
    width: 100%;
    height: 60px;
    overflow: hidden;
    object-fit: cover;
    object-position: bottom 25% left 0%;
`
const H2 = styled.h2`
    font-size: 1.1rem;
`
const H4 = styled.h4`
    color: grey;
    font-size: 0.8rem;
`
const SidebarStats = styled.div`
   padding: 10px;
   margin-bottom: 10px;
   border: 1px solid lightgrey;
   background-color: white;
   border-bottom-left-radius: 10px;
   border-bottom-right-radius: 10px;
`
const Stat = styled.div`
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
`
const P = styled.p`
    color: grey;
    font-size: 0.8rem;
    font-weight: 600;
`
const Span = styled.span`
    font-weight: bold;
    color: #0a66c2;

`
const SidebarBottom = styled.div`
    text-align: left;
    padding: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 10px;
    margin-top: 10px;
`
const H3 = styled.h3`
    font-size: 1rem;
    color: grey;
`
const RecentItem = styled.div`
    display: flex;
    font-size: 0.9rem;
    cursor: pointer;
    color: grey;
    font-weight: bolder;
    padding: 5px;
    margin-bottom: 5px;
    &:hover, >p:hover {
       background-color: whitesmoke;
       border-radius: 10px;
       cursor: pointer;
       color: black !important;
    }
`
const RecentHash = styled.span`
    margin: 0 5px 0 10px;
`

export default function Sidebar() {
    const user = useSelector(selectUser);


    const recentItem = (topic) => (
        <RecentItem>
            <RecentHash>#</RecentHash>
            <P>{topic}</P>
        </RecentItem>
    )


    return (
        <SidebarContainer>
            <SidebarTop>
                <Img src={img2} />
                <Avatar src={user.photoUrl} />
                <H2>{user.displayName}</H2>
                <H4>{user.email}</H4>
            </SidebarTop>

            <SidebarStats>
                <Stat>
                    <P>Who viewed you</P>
                    <Span>1.234</Span>
                </Stat>
                <Stat>
                    <P>Views on post</P>
                    <Span>234</Span>
                </Stat>
            </SidebarStats>

            <SidebarBottom>
                <H3>Recent</H3>
                {recentItem('reactjs')}
                {recentItem('programming')}
                {recentItem('softwareengineering')}
                {recentItem('design')}
                {recentItem('developer')}
            </SidebarBottom>
        </SidebarContainer>
    )
}
